import { UserEmail } from '../domain/UserEmail';
import { UserPassword } from '../domain/UserPassword';
import { UserRepository } from '../domain/UserRepository';
import { LoginUserRequest } from './Request/LoginUserRequest';
import { passwordEncrypted } from './PasswordOperations';

export class UserLogin {
  constructor(private repository: UserRepository) {}

  async run(request: LoginUserRequest) {
    return this.repository.login(new UserEmail(request.email), new UserPassword(passwordEncrypted(request.password)));
  }
}